<template>
    <div class="card my-4">
        <h5 class="card-header">Skills</h5>
        <div class="card-body">
            <div class="row">
                <div class="col-lg-6">
                    <ul class="list-unstyled mb-0">
                        <li v-for="item in skills">
                            <a href="#">{{item}}</a>
                        </li>
                    </ul>
                </div>
                <div class="col-lg-6">
                    <ul class="list-unstyled mb-0">
                        <li v-for="item in tools">
                            <a href="#">{{item}}</a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            skills: [
                "C#", "Java", "Web Design"
            ],
            tools: ["WPF", "VUE.JS", "Scrum"]
        }
    }
}
</script>

<style>

</style>



